export class Player {
  constructor(x, y, size, ctx) {
    this.x = x;
    this.y = y;
    this.size = size;
    this.ctx = ctx;
    this.speed = 5;
    this.dx = 0;
    this.dy = 0;
  }

  draw() {
    this.ctx.beginPath();
    this.ctx.fillStyle = "#88FF88";
    this.ctx.strokeStyle = "#000";
    this.ctx.arc(this.x, this.y, this.size, 0, 2 * 3.15);
    this.ctx.fill();
    this.ctx.stroke();
  }
  move() {
    this.x += this.dx;
    this.y += this.dy;
  }

  eat(enemies) {
    for (let i = 0; i < enemies.length; i++) {
      let deltaX = Math.abs(enemies[i].x - this.x);
      let deltaY = Math.abs(enemies[i].y - this.y);
      if (
        Math.sqrt(deltaX * deltaX + deltaY * deltaY) <
        enemies[i].size + this.size
      ) {
        if (enemies[i].size > this.size) {
          return "die";
        }
        enemies.splice(i, 1);
        this.grow();
        return true;
      }
    }
  }

  grow() {
    this.size += 1;
  }
}
