export class Enemy {
  constructor(x, y, size, dx, ctx) {
    this.x = x;
    this.y = y;
    this.size = size;
    this.ctx = ctx;
    this.dx = dx;
  }

  draw() {
    this.ctx.beginPath();
    this.ctx.fillStyle = "rgb(" + (255 - this.size * 3) + ",0,0)";
    this.ctx.strokeStyle = "#000";
    this.ctx.arc(this.x, this.y, this.size, 0, 2 * 3.15);
    this.ctx.fill();
    this.ctx.stroke();
  }

  move() {
    this.x += this.dx;
  }
}
