import { Player } from "./Player.js";
import { Enemy } from "./Enemy.js";

const canvas = document.getElementById("game");
const ctx = canvas.getContext("2d");
const FPS = 30;

const screenw = 700;
const screenh = 400;
canvas.width = screenw;
canvas.height = screenh;
canvas.style.border = "1px solid black";

document.addEventListener("keydown", keyDownHandler, false);
document.addEventListener("keyup", keyUpHandler, false);

function keyDownHandler(e){
  e.preventDefault();
  if (e.key === "right" || e.key === "ArrowRight"){
    player.dx = player.speed;
  }
  if (e.key === "left" || e.key === "ArrowLeft"){
    player.dx = -player.speed;
  }
  if (e.key === "up" || e.key === "ArrowUp"){
    player.dy = -player.speed;
  }
  if (e.key === "down" || e.key === "ArrowDown"){
    player.dy = player.speed;
  }
}
function keyUpHandler(e){
  e.preventDefault();
  if (e.key === "right" || e.key === "ArrowRight"){
    if(player.dx === player.speed){
      player.dx = 0;
    }
  }
  if (e.key === "left" || e.key === "ArrowLeft"){
    if(player.dx === -player.speed){
      player.dx = 0;
    }
  }
  if (e.key === "up" || e.key === "ArrowUp"){
    if(player.dy === -player.speed){
      player.dy = 0;
    }
  }
  if (e.key === "down" || e.key === "ArrowDown"){
    if(player.dy === player.speed){
      player.dy = 0;
    }
  }
}


let player = new Player(200,200,10,ctx);
let enemies = [];
const numberOfEnemies = 10;
const enemyRespawnRadius = 200;
const enemySpawnRadius = 600;

function randInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min) + min); 
}

function drawGameObjects(){
  ctx.clearRect(0,0,screenw,screenh);
  player.draw();
  for(let i = 0; i < enemies.length; i++){
    enemies[i].draw();
  }
}

function moveGameObjects(){
  player.move();
  for(let i = 0; i < enemies.length; i++){
    enemies[i].move()
    if(enemies[i].x > screenw + enemySpawnRadius || enemies[i].x < 0 - enemySpawnRadius){
      enemies.splice(i,1);
      spawnEnemy();
    }
  }
  let ate = player.eat(enemies);
  if(ate === true){
    player.grow();
    spawnEnemy();
  }else if(ate === "die"){
    console.log("died");
    setUp();
  }
}

function spawnEnemy(){
  let x = 0;
  if(Math.random()>0.5){
    x = randInt(screenw,screenw+enemySpawnRadius);
  }else{
    x = randInt(-enemySpawnRadius,0);
  }
  let y = randInt(10,screenh-10);
  let dx = 0;
  if(x<=screenw/2){
    dx = randInt(1,5);
  }else{
    dx = -randInt(1,5);
  }
  enemies.push(new Enemy(x,y,randInt(1,player.size+20),dx,ctx));
}

function setUp(){
  player.x = screenw/2;
  player.y = screenh/2;
  player.size = 10;
  enemies = [];
  while(enemies.length < numberOfEnemies){
    spawnEnemy();
  }
}

setUp();
setInterval(function(){
  drawGameObjects();
  moveGameObjects();
},1000/FPS)